<template>
  <div style="position:relative;height: 100%">
    <el-row style="text-align:left;background: #fff;padding-top: 10px">
      <el-col :span="18">
        <el-button  @click="dialogFormVisible = true" style="margin-left: 10px;">+</el-button>
        <el-dialog title="添加教育讯息" :visible.sync="dialogFormVisible" :before-close="closeDialog" >
          <el-form :model="form" ref="form">
            <el-form-item label="讯息内容" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.noticeContent" autocomplete="off" placeholder="请输入讯息内容"></el-input>
            </el-form-item>
            <el-form-item label="创建人" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.piblisher" autocomplete="off" placeholder="请输入创建人"></el-input>
            </el-form-item>
            <el-form-item label="图片地址" style="width:600px" :label-width="formLabelWidth">
              <el-upload
                action="http://localhost:8080/upload"
                list-type="picture-card"
                :multiple = false
                :limit=1
                :on-success="successuploadadd">
                <i class="el-icon-plus"></i>
              </el-upload>
              <!--              <el-input v-model="form.firstUrl" autocomplete="off" placeholder="请上传菜品图片"></el-input>-->
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="exit">取 消</el-button>
            <el-button type="primary" @click="btn">确 定</el-button>
          </div>
        </el-dialog>

        <el-dialog title="添加教育讯息" :visible.sync="dialogFormVisibletwo" :before-close="closeDialog" >
          <el-form :model="form" ref="form">
            <el-form-item label="讯息内容" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.noticeContent" autocomplete="off" placeholder="请输入讯息内容"></el-input>
            </el-form-item>
            <el-form-item label="创建人" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.piblisher" autocomplete="off" placeholder="请输入创建人"></el-input>
            </el-form-item>
            <el-form-item label="图片地址" style="width:600px" :label-width="formLabelWidth">
              <el-upload
                action="http://localhost:8080/upload"
                list-type="picture-card"
                :multiple = false
                :limit=1
                :on-success="successuploadadd">
                <i class="el-icon-plus"></i>
              </el-upload>
              <!--              <el-input v-model="form.firstUrl" autocomplete="off" placeholder="请上传菜品图片"></el-input>-->
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="exittwo">取 消</el-button>
            <el-button type="primary" @click="btntwo">确 定</el-button>
          </div>
        </el-dialog>
      </el-col>
<!--      <el-col :span="6">-->
<!--        <el-input style="width: 300px;margin-right: 0px" placeholder="输入职位查询(不输入内容默认搜索全部)" v-model="userName"></el-input>-->
<!--&lt;!&ndash;        <el-button @click="findListClick">搜索</el-button>&ndash;&gt;-->
<!--      </el-col>-->
    </el-row>
  <el-table
    :data="tableData"
    style="width: 100%;height: 90%">
    <el-table-column
      label="ID"
      type="index">
    </el-table-column>
        <el-table-column
      label="图片"
      prop="imageUrl">
      <template slot-scope="scope">
　　　　   <img  :src="scope.row.imageUrl" width="200" height="100" class="head_pic"/>
　　   </template>
    </el-table-column>
        <el-table-column
      label="讯息内容"
      prop="noticeContent">
    </el-table-column>
    <el-table-column
      label="创建人"
      prop="publisher">
    </el-table-column>
    <el-table-column
      label="创建时间"
      prop="createTime">
    </el-table-column>
    <el-table-column
      align="right">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEidt(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
    <div style="height: 30px;text-align: center;line-height:30px">
      @copyright在线教育后台管理系统
    </div>

  </div>
</template>

<script>
import Pagination from './pagination.vue'
import {eduNoticeList,eduNoticeInsert,eduNoticeupdate,eduNoticedelete} from '@/api/user.js';
    export default {
      name: "result",
      components: {
        Pagination
      },
      data() {
        return {
          tableData: [],
          search: '',
          pageNum: 1,
          pageSize: 5,
          total: 0,
          count: '',
          parms:{
            pageNum:1,
            pageSize:5
          },
          selectList:{
            userId:'',
            status:null
          },
          value:null,
          userName:'',
          onePassword:'',
          userId:'',
          dialogFormVisible: false,
          dialogFormVisibletwo:false,
          form: {
            id:'',
            publisher:'',
            noticeContent:'',
            imageUrl:'',
            createTime:'',
          },
          formLabelWidth: '120px'
        }
      },
      mounted() {
        this.selectAllUserList();
      },
      methods: {
           successuploadadd(file, fileList){
            this.form.imageUrl =file.data
          },
        handleEidt(index, row) {
          console.log(index, row);
          this.form.id = row.id
          this.form.bannerTitle=row.bannerTitle
          this.form.isEnable = row.isEnable
          this.form.bannerOrder=row.bannerOrder
          this.dialogFormVisibletwo = true;
          this.selectAllUserList();
        },
        handleDelete(index, row) {
          console.log(index, row);
          var r=confirm("是否确定删除？");
          if (r===true){
            eduNoticedelete(row.id).then((response)=>{
              if (response.data.code === 0){
                this.selectAllUserList();
              }
            })

          }else{

          }
        },
        handlechange(index, row) {
          console.log(index, row);
          this.userId = row.userId
          this.dialogFormVisiblethree= true
        },
        handleCurrentChange (val) {
          console.log(val)//页码
          this.parms.pageNum = val
          this.selectAllUserList();
        },
        handleSizeChange (val) {
          console.log(val)
          this.parms.pageSize = val//页数
          this.selectAllUserList();
        },
        //查询所有banner;
        selectAllUserList(){
          eduNoticeList(this.parms).then((response)=>{
          console.log(response)
            this.tableData = response.data.data.records
            this.total = response.data.data.total
          })
        },
        btn(){
          this.dialogFormVisible = false
          if(this.form.isEnable === '可用'){
            this.form.isEnable =1 
          }else{
            this.form.isEnable = 0
          }
          eduNoticeInsert(this.form).then((response)=>{
            if (response.data.code === 0){
              this.$message.success("添加成功");
              this.resetFormData();
              this.selectAllUserList();
              location.reload()
            }
          })

        },
        btntwo(){
          this.dialogFormVisibletwo = false
          eduNoticeupdate(this.form).then((response)=>{
            if (response.data.code === 0){
              this.selectAllUserList()
              this.$message.success("修改成功");
              this.resetFormData()
            }
          })

        },
        exitthree(form){
          this.dialogFormVisiblethree = false
          this.resetFormData()
        },
        exittwo(form){
          this.dialogFormVisibletwo = false
          this.resetFormData()
        },
        resetFormData(){
          this.form.bannerTitle ='';
          this.form.imageUrl ='';
          this.form.bannerOrder ='';
          this.form.isEnable ='';
        },
        exit(form){
          this.dialogFormVisible = false
          this.resetFormData()
          location.reload()
        },
        closeDialog(done){
          this.$confirm('确认关闭？')
            .then(_ => {
              done();
              location.reload();
            })
            .catch(_ => { });
        }
      },
    }
</script>

<style scoped>

</style>



