<template>
  <div style="position:relative;height: 100%">
    <el-row style="text-align:left;background: #fff;padding-top: 10px">
      <el-col :span="18">
      </el-col>
<!--      <el-col :span="6">-->
<!--        <el-input style="width: 300px;margin-right: 0px" placeholder="输入职位查询(不输入内容默认搜索全部)" v-model="userName"></el-input>-->
<!--        &lt;!&ndash;        <el-button @click="findListClick">搜索</el-button>&ndash;&gt;-->
<!--      </el-col>-->
    </el-row>
    <el-table
      :data="tableData"
      style="width: 100%;height: 90%">
      <el-table-column
        label="ID"
        type="index">
      </el-table-column>
      <el-table-column
        label="姓名"
        prop="realName">
      </el-table-column>
      <el-table-column
        label="电话"
        prop="userPhone">
      </el-table-column>
      <el-table-column
        label="留言内容"
        prop="leaveMessage">
      </el-table-column>
      <el-table-column
        align="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="height: 30px;text-align: center">
      <pagination :currentPage="pageNum" :total=total :limit="pageSize" :small="false"
                  @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" style="text-align: center"/>
    </div>

  </div>
</template>

<script>
  import Pagination from './pagination.vue'
  import {liuyaneduleavenlist,liuyaneduleavendelete} from '@/api/user.js';
  export default {
    name: "result",
    components: {
      Pagination
    },
    data() {
      return {
        tableData: [],
        search: '',
        pageNum: 1,
        pageSize: 5,
        total: 0,
        count: '',
        parms:{
          pageNum:1,
          pageSize:12
        },
        selectList:{
          userId:'',
          status:null
        },
        value:null,
        userName:'',
        onePassword:'',
        userId:'',
        dialogFormVisible: false,
        dialogFormVisibletwo:false,
        dialogFormVisiblethree:false,
        form: {
          goodName:'',
          goodType:'',
          buyNum:'',
          buyPlace:'',
        },
        formLabelWidth: '120px'
      }
    },
    mounted() {
      this.liuyaneduleavenlist();
    },
    methods: {
      liuyaneduleavenlist(){
          liuyaneduleavenlist().then((response)=>{
          this.tableData = response.data.data.records
          this.total = response.data.data.total
        })
      },
      handleDelete(index, row) {
        console.log(index, row);
        var r=confirm("是否确定删除？");
        if (r===true){
          liuyaneduleavendelete(row.id).then((response)=>{
            if (response.data.code === 0){
              this.$message.success("已删除")
              this.liuyaneduleavenlist();
            }
          })

        }else{

        }
      },
      handleCurrentChange (val) {
        console.log(val)//页码
        this.parms.pageNum = val
        this.liuyaneduleavenlist();
      },
      handleSizeChange (val) {
        console.log(val)
        this.parms.pageSize = val//页数
        this.liuyaneduleavenlist();
      },
      btn(){
        this.dialogFormVisible = false
    

      },
      btntwo(){
        this.dialogFormVisibletwo = false
    

      },
      exitthree(form){
        this.dialogFormVisiblethree = false
        this.resetFormData()
      },
      exittwo(form){
        this.dialogFormVisibletwo = false
        this.resetFormData()
      },
      resetFormData(){
        this.form.goodName ='';
        this.form.goodType ='';
        this.form.buyNum ='';
        this.form.buyPlace ='';
      },
      exit(form){
        this.dialogFormVisible = false
        this.resetFormData()
      },
      closeDialog(done){
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
            location.reload();
          })
          .catch(_ => { });
      }
    },
  }
</script>

<style scoped>

</style>



