<template>
  <div class="bg_color" >
    <!--表单-->
    <el-form style="width:23%;margin-right: 140px;position: relative;left: 60px" :model="loginForm" status-icon :rules="rules" ref="loginForm" label-width="100px" class="demo-ruleForm">
      <el-form-item  >
        <!-- <h3 style="color: #ffffff;text-align: center;font-weight: bold;margin-bottom: 30px;font-size: 30px">线上教育</h3> -->
      </el-form-item>
      <el-form-item  label="用户名" prop="username" >
        <el-input type="text" v-model="loginForm.username" autocomplete="off"  placeholder="账号为teacher" ></el-input>
      </el-form-item>
      <el-form-item  label="密码" prop="password">
        <el-input type="password" v-model="loginForm.password" autocomplete="off"  placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button style="width: 100%"  plain type="primary" @click="submitForm('loginForm')" >登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  import {adminlogin} from '@/api/user.js';
  export  default {
    data(){
      return{
//      登录表单的数据
        loginForm: {
          username: '',
          password : '',
        },
        rules: {
          username:[
            { required: true, message: '请输入账号', trigger: 'blur' },
            { min: 3, max: 18, message: '长度在 3 到 18 个字符', trigger: 'blur' }
          ],
          password :[
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 5, max: 18, message: '长度在 5 到 18 个字符', trigger: 'blur' }
          ]
        },
      }
    },
    methods:{
      submitForm:function(formName) {
        // this.centerDialogVisibleTwo = false;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //登录接口;
            adminlogin(this.loginForm.username,this.loginForm.password).then((response)=>{
              if(response.data.code === 0){
        const h = this.$createElement;
        this.$notify({
          title: '管理员登入',
          message: h('i', { style: 'color: teal'}, '登录成功')
        });
        this.$router.push('./index')
              }else{
                this.$message.error(response.data.msg);
              }
            })
          }
        });
      },
    },
    created () {
    },
  }
</script>
<style scoped>
  .bg_color{
    background: url("./../assets/loginss.jpg") no-repeat;
    height: 100vh !important;
    background-position: center center;
    background-size: cover;
    display: flex !important;
    flex-direction: column !important;
    justify-content: center !important;
    align-items: center;
  }

</style>



