<template>
  <div style="position:relative;height: 100%">
    <el-row style="text-align:left;background: #fff;padding-top: 10px">
      <el-col :span="16">
        <el-button  @click="dialogFormVisible = true" style="margin-left: 10px;">+</el-button>
        <el-dialog title="课程添加" :visible.sync="dialogFormVisible" :before-close="closeDialog" >
          <el-form :model="form" ref="form">
            <el-form-item label="课程名" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.courseName" autocomplete="off" placeholder="请输课程名"></el-input>
            </el-form-item>
            <el-form-item label="上传图片" style="width:600px" :label-width="formLabelWidth">
              <el-upload
                action="http://localhost:8080/upload"
                list-type="picture-card"
                :on-preview="handlePictureCardPreview"
                :on-change="handleChange"
                :multiple = false
                :limit=1
                :on-remove="handleRemove"
                :on-success="successuploadadd">
                <i class="el-icon-plus"></i>
              </el-upload>
              <!--              <el-input v-model="form.firstUrl" autocomplete="off" placeholder="请上传菜品图片"></el-input>-->
            </el-form-item>
            <el-form-item label="课程简介" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.courseInfo" autocomplete="off" placeholder="请输入课程简介"></el-input>
            </el-form-item>
            <el-form-item label="创建人" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.courseMaster" autocomplete="off" placeholder="请输入创建人"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="exit">取 消</el-button>
            <el-button type="primary" @click="btn">确 定</el-button>
          </div>
        </el-dialog>
        <el-dialog title="修改基本信息" :visible.sync="dialogFormVisibletwo" :before-close="closeDialog" >
          <el-form :model="form" ref="form">
            <el-form-item label="课程名" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.courseName" autocomplete="off" placeholder="请输课程名"></el-input>
            </el-form-item>
            <el-form-item label="上传图片" style="width:600px" :label-width="formLabelWidth">
              <el-upload
                action="http://localhost:8080/upload"
                list-type="picture-card"
                :on-preview="handlePictureCardPreview"
                :on-change="handleChange"
                :multiple = false
                :limit=1
                :on-remove="handleRemove"
                :on-success="successuploadadd">
                <i class="el-icon-plus"></i>
              </el-upload>
              <!--              <el-input v-model="form.firstUrl" autocomplete="off" placeholder="请上传菜品图片"></el-input>-->
            </el-form-item>
            <el-form-item label="课程简介" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.courseInfo" autocomplete="off" placeholder="请输入课程简介"></el-input>
            </el-form-item>
            <el-form-item label="创建人" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.courseMaster" autocomplete="off" placeholder="请输入创建人"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="exittwo">取 消</el-button>
            <el-button type="primary" @click="btntwo">确 定</el-button>
          </div>
        </el-dialog>
         <el-dialog title="添加章节" :visible.sync="dialogFormVisiblethree" :before-close="closeDialog" >
          <el-form :model="form" ref="form">
            <el-form-item label="章节名称" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="formtwo.chapterName" autocomplete="off" placeholder="请输课程名"></el-input>
            </el-form-item>
            <el-form-item label="上传视频" style="width:600px" :label-width="formLabelWidth">
               <el-upload class="avatar-uploader"
                           action="http://localhost:8080/upload"
                           v-bind:data="{FoldPath:'上传目录',SecretKey:'安全验证'}"
                           v-bind:on-progress="uploadVideoProcess"
                           v-bind:on-success="handleVideoSuccess"
                           v-bind:before-upload="beforeUploadVideo"
                           v-bind:show-file-list="false">
                    <video v-if="videoForm.showVideoPath !='' && !videoFlag"
                           v-bind:src="videoForm.showVideoPath"
                           class="avatar video-avatar"
                           controls="controls">
                        您的浏览器不支持视频播放
                    </video>
                    <i v-else-if="videoForm.showVideoPath =='' && !videoFlag"
                       class="el-icon-plus avatar-uploader-icon"></i>
                    <el-progress v-if="videoFlag == true"
                                 type="circle"
                                 v-bind:percentage="videoUploadPercent"
                                 style="margin-top:7px;"></el-progress>
                </el-upload>
                 <p class="Upload_pictures">
        <span></span>
        <span>最多可以上传1个视频，建议大小50M，推荐格式mp4</span>
    </p>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="exitthree">取 消</el-button>
            <el-button type="primary" @click="btnthree">确 定</el-button>
          </div>
        </el-dialog>

        <el-dialog title="查看章节" :visible.sync="dialogFormVisiblefive" :before-close="closeDialog" >
           <el-table
      :data="tableDatatwo"
      style="width: 100%;height: 90%">
      <el-table-column
        label="ID"
        type="index">
      </el-table-column>
      <el-table-column
        label="章节名称"
        prop="chapterName">
      </el-table-column>
      <el-table-column
        width="340px"
        align="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleDeletezhangjie(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
          <div slot="footer" class="dialog-footer">
            <el-button @click="exitfive">取 消</el-button>
            <el-button type="primary" @click="btnfive">确 定</el-button>
          </div>
        </el-dialog>

      <el-dialog title="签到列表" :visible.sync="dialogFormVisiblesix" :before-close="closeDialog" >
           <el-table
      :data="tableDatathree"
      style="width: 100%;height: 90%">
      <el-table-column
        label="ID"
        type="index">
      </el-table-column>
      <el-table-column
        label="姓名"
        prop="realName">
      </el-table-column>
      <el-table-column
        label="是否签到(1代表签到，0代表未签到)"
        prop="signedNumber">
      </el-table-column>
      <el-table-column
        width="340px"
        align="right">
      </el-table-column>
    </el-table>
          <div slot="footer" class="dialog-footer">
            <el-button @click="exitsix">取 消</el-button>
            <el-button type="primary" @click="btnsix">确 定</el-button>
          </div>
        </el-dialog>
        <!-- 修改详细信息 -->
                    </el-col>
 <el-col :span="8">
<el-input style="width: 300px;margin-right: 0px" placeholder="输入课程名查询" v-model="phoneName"></el-input>
 <el-button @click="findListClick">搜索</el-button>
     </el-col>
    </el-row>
    <el-table
      :data="tableData"
      style="width: 100%;height: 90%">
      <el-table-column
        label="ID"
        type="index">
      </el-table-column>

      <el-table-column
        label="封面图片"
        prop="courseImgUrl"
        >
      <template slot-scope="scope">
　　　　   <img  :src="scope.row.courseImgUrl" width="200" height="100" class="head_pic"/>
　　   </template>
      </el-table-column>
      <el-table-column
        label="课程名"
        prop="courseName">
      </el-table-column>
      <el-table-column
        label="课程简介"
        prop="courseInfo">
      </el-table-column>
        <el-table-column
        label="创建人"
        prop="courseMaster">
      </el-table-column>
      <el-table-column
        label="发布时间"
        prop="courseDate">
      </el-table-column>
      <el-table-column
        width="465px"
        align="right">
        <template slot-scope="scope">
                    <el-button
            size="mini"
            @click="handleAdd(scope.$index, scope.row)">添加章节</el-button>
           <el-button
            size="mini"
            @click="handlechakan(scope.$index, scope.row)">查看章节</el-button>
            <el-button
            size="mini"
            @click="handleqiandao(scope.$index, scope.row)">查看签到</el-button>
          <el-button
            size="mini"
            @click="handleEidt(scope.$index, scope.row)">编辑</el-button>
          <el-button
            size="mini"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination :currentPage="pageNum" :total=total :limit="pageSize" :small="false"
                  @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" style="text-align: center"/>
  

  </div>
</template>

<script>
  import Pagination from './pagination.vue'
  import {
          adminphonelistmain,
          adminphoneserch,
          adminupdatephone,
          admindeletephone,
          adminaddphone,
          admingetdetailsphoneid,
          admindetailsphone,
          getCourseInfo,
          adminupdatephonedetails,
          chapterdelete,
          qiandaocourseitemlist
    //       producetaskinsertList,
    //       menucaipin,
    //       orderinfomenudelete,
    //       updateStay,
    // insertMenuList,
    // menuupdate
          } from '@/api/user.js';
  export default {
    name: "result",
    components: {
      Pagination
    },
    data() {
      return {
                    videoFlag: false,
            //是否显示进度条
            videoUploadPercent: "",
            //进度条的进度，
            isShowUploadVideo: false,
            //显示上传按钮
            videoForm: {
                showVideoPath: ''
            },
        upFile:'',
        courseImgUrl: '',
        value1:[],
        tableData: [],
        tableDatatwo:[],
        tableDatathree:[],
        search: '',
        pageNum: 1,
        pageSize: 10,
        total: 0,
        count: '',
        phoneName:'',
        selectList:{
          userId:'',
          status:null
        },
        value:null,
        userName:'',
        dialogFormVisible: false,
        dialogFormVisibletwo:false,
        dialogFormVisiblefour:false,
        dialogFormVisiblethree:false,
        dialogFormVisiblefive:false,
        dialogFormVisiblesix:false,
        form: {
          courseImgUrl:'',
          courseInfo:'',
          courseMaster:'',
          courseName:'',
          chapterCount:'',
          courseId:'',
          coursePopularity:'',
          isDeleted:'',
          isEnable:'',
        },
        formtwo:{
          courseId:'',
          chapterName:'',
          chapterType:'',
          parentId:'',
          chapterOrder:'',
          videoUrl:'',
          videoDuration:''
        },
        formLabelWidth: '120px'
      }
    },
    mounted() {
      this.adminphonelistmain();
    },
    methods: {
       //上传前回调
            beforeUploadVideo(file) {
                var fileSize = file.size / 1024 / 1024 < 50;
                if (['video/mp4', 'video/ogg', 'video/flv', 'video/avi', 'video/wmv', 'video/rmvb', 'video/mov'].indexOf(file.type) == -1) {
                    layer.msg("请上传正确的视频格式");
                    return false;
                }
                if (!fileSize) {
                    layer.msg("视频大小不能超过50MB");
                    return false;
                }
                this.isShowUploadVideo = false;
            },
            //进度条
            uploadVideoProcess(event, file, fileList) {
                this.videoFlag = true;
                this.videoUploadPercent = file.percentage.toFixed(0) * 1;
            },
            //上传成功回调
            handleVideoSuccess(res, file) {

                this.isShowUploadVideo = true;
                this.videoFlag = false;
                this.videoUploadPercent = 0;
                
                //前台上传地址
                //if (file.status == 'success' ) {
                //    this.videoForm.showVideoPath = file.url;
                //} else {
                //     layer.msg("上传失败，请重新上传");
                //}

                //后台上传地址
                if (res.code == 0) {
                    this.formtwo.videoUrl =file.response.data
                    this.$message.success('视频已上传到浏览器，请确定')
                }
            },
      handleAvatarSuccess(res, file) {
        console.log(file.response.data[0])
        this.form.courseImgUrl=file.response.data[0]
        // this.firstUrl = URL.createObjectURL(file.raw);
    
      },
//上传图标事件
      handleRemove(file, fileList) {
        // console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      handlePictureCardPreviewthree(){
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      // uploadchange(item){
      //   const fileObj = item.file
      //   const form01 = new FormData()
      //   form01.append('file', fileObj)
      //   menucaipin(form01).then((response)=>{
      //   })
      // },
      //上传文件变化监听
      handleChange(file, fileList){
        // console.log(file.response)
        this.upFile = file.raw
      },
    successupload(file, fileList){
      this.form.courseImgUrl =file.data
    },
    successuploadadd(file, fileList){
      this.form.courseImgUrl =file.data
    },
     successuploadaddthree(file, fileList){
      this.formtwo.videoUrl =file.data
    },
      getSTime (val) {
        console.log(val)
        this.value1 = val
        var d = new Date(this.value1[0])
        var startTime =
          d.getFullYear() +
          '-' +
          (d.getMonth() + 1) +
          '-' +
          d.getDate()
        console.log(startTime)
        this.form.startTime = startTime
        var d2 = new Date(this.value1[1])
        var endTime =
          d2.getFullYear() +
          '-' +
          (d2.getMonth() + 1) +
          '-' +
          d2.getDate()
        console.log(endTime)
        this.form.endTime = endTime
        // console.log(this.prams)
      },
      handleAdd(index, row){
        console.log(row)
        this.formtwo.courseId = row.courseId
        this.dialogFormVisiblethree = true
      },
         handlechakan(index, row){
          getCourseInfo(row.courseId).then((response)=>{
            if (response.data.code === 0){
              this.tableDatatwo = response.data.data.chapterList
            }
          })
        // this.formtwo.courseId = row.courseId
        this.dialogFormVisiblefive = true
      },
      handleqiandao(index, row){
        console.log(row)
          qiandaocourseitemlist(row.courseId).then((response)=>{
            if (response.data.code === 0){
             this.tableDatathree = response.data.data
            }
          })
        this.dialogFormVisiblesix= true
      },
      handleEidt(index, row) {
        this.form.courseImgUrl=row.courseImgUrl
        this.form.courseInfo=row.courseInfo
        this.form.courseMaster=row.courseMaster
        this.form.courseName=row.courseName
        this.form.chapterCount=row.chapterCount
        this.form.courseId=row.courseId
        this.form.coursePopularity=row.coursePopularity
        this.form.isDeleted=row.isDeleted
        this.form.isEnable=row.isEnable
        this.dialogFormVisibletwo = true;
        // this.adminphonelistmain();
      },
      handleDelete(index, row) {
        console.log(index, row);
        var r=confirm("是否确定删除？");
        if (r===true){
          admindeletephone(row.courseId).then((response)=>{
            if (response.data.code === 0){
              this.adminphonelistmain();
            }
          })

        }else{

        }
      },
      handleDeletezhangjie(index, row){
          console.log(index, row);
        var r=confirm("是否确定删除？");
        if (r===true){
          chapterdelete(row.id).then((response)=>{
            if (response.data.code === 0){
             this.$router.go(0);
               this.$message.error("删除成功")
            }
          })

        }else{

        }
      },
      handleCurrentChange (val) {
        console.log(val)//页码
        this.pageNum = val
        this.adminphonelistmain();
      },
      handleSizeChange (val) {
        console.log(val)
        this.pageSize = val//页数
        this.adminphonelistmain();
      },

      //查询所有手机;
      adminphonelistmain(){
        adminphonelistmain(this.pageNum,this.pageSize,this.phoneName).then((response)=>{
          this.tableData = response.data.data.records
          this.total = response.data.data.total
        })
      },
      //查询用户
      findListClick(){
        adminphoneserch(this.phoneName).then((response)=>{
          this.adminphonelistmain()
        })
      },
      btn(){
        this.dialogFormVisible = false
        adminaddphone(this.form).then((response)=>{
          if (response.data.code === 0){
            this.$message.success("添加成功");
            this.adminphonelistmain();
          }
          })


      },
      btntwo(){
          this.dialogFormVisibletwo = false
        adminupdatephone(this.form).then((response)=>{
          if (response.data.code === 0){
            this.adminphonelistmain()
            this.$message.success("修改成功");
            this.resetFormData()
           location.reload()
          }
        });
      },
      btnthree(){
        this.dialogFormVisiblethree = false
            admindetailsphone(this.formtwo).then((response)=>{
          // if (response.data.code === 0){
          //   this.adminphonelistmain()
          //   this.$message.success("添加成功");
          //   this.resetFormData()
          //  location.reload()
          // }
        });
      },
      exitthree(){
        this.dialogFormVisiblethree = false
      },
      btnfive(){
        this.dialogFormVisiblefive = false
            admindetailsphone(this.formtwo).then((response)=>{
          // if (response.data.code === 0){
          //   this.adminphonelistmain()
          //   this.$message.success("添加成功");
          //   this.resetFormData()
          //  location.reload()
          // }
        });
      },
      exitfive(){
        this.dialogFormVisiblefive = false
      },
      
      btnsix(){
        this.dialogFormVisiblesix = false
      
      },
      exitsix(){
        this.dialogFormVisiblesix = false
      },

      exittwo(form){
        this.dialogFormVisibletwo = false
        this.resetFormData()
      },
      btnfour(){
        this.dialogFormVisiblefour = false
        menuupdate(this.form).then((response)=>{
          if (response.data.code === 200){
            this.adminphonelistmain()
            this.$message.success("修改成功");
            this.resetFormData()
          }
        });
      },
      exitfour(form){
        this.dialogFormVisiblefour = false
        this.resetFormData()
      },
      resetFormData(){
        this.form.courseImgUrl ='';
        this.form.courseInfo ='';
        this.form.courseMaster ='';
        this.form.courseName ='';
      },
      exit(form){
        this.dialogFormVisible = false
        this.resetFormData()
      },
      closeDialog(done){
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
            location.reload();
          })
          .catch(_ => { });
      }
    },
  }
</script>

<style scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



