<template>
  <div style="position:relative;height: 100%">
        <el-row style="text-align:left;background: #fff;padding-top: 10px">
      <el-col :span="16">
        <el-button  @click="dialogFormVisible = true" style="margin-left: 10px;">+</el-button>
        <el-dialog title="添加用户" :visible.sync="dialogFormVisible" :before-close="closeDialog" >
          <el-form :model="form" ref="form">
            <el-form-item label="用户名" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.username" autocomplete="off" placeholder="请输用户名"></el-input>
            </el-form-item>
            <el-form-item label="手机号" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.userPhone" autocomplete="off" placeholder="请输入手机号"></el-input>
            </el-form-item>
            <el-form-item label="性别" style="width:600px" :label-width="formLabelWidth">
               <el-select v-model="form.userSex" placeholder="请选择性别">
                <el-option label="女" value="0"></el-option>
                <el-option label="男" value="1"></el-option>
              </el-select>
            </el-form-item>
             <el-form-item label="密码" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.password" autocomplete="off" placeholder="请输入密码" type="password"></el-input>
            </el-form-item>
            <el-form-item label="真实姓名" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.realName" autocomplete="off" placeholder="请输入真实姓名"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="exit">取 消</el-button>
            <el-button type="primary" @click="btn">确 定</el-button>
          </div>
          
        </el-dialog>
                <el-dialog title="添加用户" :visible.sync="dialogFormVisibletwo" :before-close="closeDialog" >
          <el-form :model="form" ref="form">
            <el-form-item label="用户名" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.username" autocomplete="off" placeholder="请输用户名"></el-input>
            </el-form-item>
            <el-form-item label="手机号" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.userPhone" autocomplete="off" placeholder="请输入手机号"></el-input>
            </el-form-item>
            <el-form-item label="性别" style="width:600px" :label-width="formLabelWidth">
               <el-select v-model="form.userSex" placeholder="请选择性别">
                <el-option label="女" value="0">
                </el-option>
                <el-option label="男" value="1"></el-option>
              </el-select>
            </el-form-item>
             <el-form-item label="密码" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.password" autocomplete="off" placeholder="请输入密码" type="password"></el-input>
            </el-form-item>
            <el-form-item label="真实姓名" style="width:600px" :label-width="formLabelWidth">
              <el-input v-model="form.realName" autocomplete="off" placeholder="请输入真实姓名"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="exittwo">取 消</el-button>
            <el-button type="primary" @click="btntwo">确 定</el-button>
          </div>
        </el-dialog>
      </el-col>
        </el-row>
    <el-table
      :data="tableData"
      style="width: 100%;height: 90%">
      <el-table-column
        label="ID"
        type="index">
      </el-table-column>
      <el-table-column
        label="用户名"
        prop="username">
      </el-table-column>
      <el-table-column
        label="手机号"
        prop="userPhone">
      </el-table-column>
      <el-table-column
        label="密码"
        prop="password">
      </el-table-column>
      <el-table-column
        label="性别"
        prop="userSex">
        <template slot-scope="scope">
          <span v-if="scope.row.userSex === 1">男</span>
          <span v-else>女</span>
        </template>
      </el-table-column>
         <el-table-column
        label="真实姓名"
        prop="realName">
      </el-table-column>
        <el-table-column
        label="是否签到"
        prop="">
      </el-table-column>
      <el-table-column
        align="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="handlechangeuser(scope.$index, scope.row)">更新</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="height: 30px;text-align: center">
      <pagination :currentPage="pageNum" :total=total :limit="pageSize" :small="false"
                  @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" style="text-align: center"/>
    </div>

  </div>
</template>

<script>
  import Pagination from './pagination.vue'
  import {adminlistyonghu,admindeleteUser,useradd,userupdate} from '@/api/user.js';
  export default {
    name: "result",
    components: {
      Pagination
    },
    data() {
      return {
        tableData: [],
        search: '',
        pageNum: 1,
        pageSize: 5,
        total: 0,
        count: '',
        parms:{
          pageNum:1,
          pageSize:12
        },
        selectList:{
          userId:'',
          status:null
        },
        value:null,
        userName:'',
        onePassword:'',
        userId:'',
        dialogFormVisible: false,
        dialogFormVisibletwo:false,
        dialogFormVisiblethree:false,
        form: {
          username:'',
          password:'',
          realName:'',
          userPhone:'',
          userSex:'',
          id:''
          
        },
        formLabelWidth: '120px'
      }
    },
    mounted() {
      this.adminlistyonghu();
    },
    methods: {
      adminlistyonghu(){
        
          adminlistyonghu().then((response)=>{
          this.tableData = response.data.data.records
          this.total = response.data.data.total
        })
      },
      handlechangeuser(index,row){
        console.log(row)
        // if(row.userSex = 0){
        //   this.form.userSex = '女'
        // }else{
        //   this.form.userSex = '男'
        // }
        this.dialogFormVisibletwo = true
        this.form.username = row.username
        this.form.password = row.password
        this.form.realName = row.realName
        this.form.userPhone = row.userPhone
        this.form.userSex = row.userSex
        this.form.id = row.id
      },
      handleDelete(index, row) {
        console.log(index, row);
        var r=confirm("是否确定删除？");
        if (r===true){
          admindeleteUser(row.id).then((response)=>{
            if (response.data.code === 0){
              this.$message.success("已删除")
              this.adminlistyonghu();
            }
          })

        }else{

        }
      },
      handleCurrentChange (val) {
        console.log(val)//页码
        this.parms.pageNum = val
        this.adminlistyonghu();
      },
      handleSizeChange (val) {
        console.log(val)
        this.parms.pageSize = val//页数
        this.adminlistyonghu();
      },
      btn(){
        this.dialogFormVisible = false
            useradd(this.form).then((response)=>{
            if (response.data.code === 0){
              this.$message.success("添加成功")
              this.adminlistyonghu();
            }
          })

      },
      btntwo(){
        this.dialogFormVisibletwo = false
          userupdate(this.form).then((response)=>{
            if (response.data.code === 0){
              this.$message.success("修改成功")
              this.adminlistyonghu();
            }
          })

      },
      exitthree(form){
        this.dialogFormVisiblethree = false
        this.resetFormData()
      },
      exittwo(form){
        this.dialogFormVisibletwo = false
        this.resetFormData()
      },
      resetFormData(){
        this.form.goodName ='';
        this.form.goodType ='';
        this.form.buyNum ='';
        this.form.buyPlace ='';
      },
      exit(form){
        this.dialogFormVisible = false
        this.resetFormData()
      },
      closeDialog(done){
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
            location.reload();
          })
          .catch(_ => { });
      }
    },
  }
</script>

<style scoped>

</style>



