<template>
  <el-container>
    <el-aside width="200px" style="height:auto">
      <el-menu
        :default-active="this.$route.path"
        router
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        text-color="#444"
        active-text-color="#444"
      style="height: 100vh">
        <el-menu-item index="/alarmMessage" style="text-align:left">
         
          <span slot="title">课程列表</span>
        </el-menu-item>
        <el-menu-item index="/result"  style="text-align:left">
      
          <span slot="title">banner列表</span>
        </el-menu-item>
        <el-menu-item index="/personnews"  style="text-align:left">
      
          <span slot="title">留言管理</span>
        </el-menu-item>
        <el-menu-item index="/test"  style="text-align:left">
        
          <span slot="title">用户列表</span>
        </el-menu-item>
        <el-menu-item index="/autoMonitor"  style="text-align:left">
          <span slot="title">教育讯息</span>
        </el-menu-item>
        <el-menu-item index="/autoMonitor"  style="text-align:left">
          <el-button slot="title" @click="tuichudenglu">退出登录</el-button>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-container>
      <!-- <el-header style="text-align: right">
        <span>{{userName}}</span>
        <el-button type="primary"  @click="exitlogin">退出登录</el-button>
      </el-header> -->
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
    export default {
        name: "index",
        data(){
        return{
          userName:sessionStorage.getItem('userName')
        }
      },
      methods: {
        tuichudenglu(){
           var r=confirm("是否退出登录？");
           if (r===true){
            this.$router.push('/login')
            sessionStorage.removeItem('userName')
            sessionStorage.removeItem('id')
          }
        },
        handleOpen(key, keyPath) {
          console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
          console.log(key, keyPath);
        },
      },
    }
</script>

<style scoped>
  .el-header, .el-footer {
    /* background-color:rgb(62, 62, 62); */
    color: #333;
    text-align: center;
    line-height: 60px;
  }

  .el-aside {
    /* background-color: rgb(62, 62, 62); */
    color: #333;
    text-align: center;
    line-height: 200px;
  }

  .el-main {
    /* background-color: #999; */
    color: #333;
    /* text-align: center; */
    line-height: 20px !important;

  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
</style>



